.cart {
  &__head {
    display: flex;
    align-items: flex-end;
    margin-bottom: 72px;
    @include x {
      margin-bottom: 64px; }
    @include m {
      margin-bottom: 56px; }
    @include a {
      display: block; } }
  &__box {
    margin-right: auto;
    @include a {
      margin: 0 0 24px; } }
  &__row {
    display: flex;
    @include x {
      display: block; } }
  &__col {
    &:first-child {
      flex-grow: 1;
      padding-right: 48px;
      @include x {
        margin-bottom: 48px;
        padding: 0; }
      @include m {
        margin-bottom: 40px; } }
    &:nth-child(2) {
      flex-shrink: 0;
      width: 416px;
      @include x {
        width: 100%; } } }
  &__item {
    display: flex;
    padding: 54px 56px;
    border-radius: 48px;
    border: 2px solid $gray;
    @include x {
      position: relative;
      padding-right: 150px; }
    @include t {
      padding-right: 56px; }
    @include m {
      display: block;
      padding: 24px;
      border-radius: 32px;
      text-align: center; }
    @include nl {
      margin-bottom: 48px;
      @include m {
        margin-bottom: 40px; } } }
  &__preview {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 192px;
    height: 192px;
    margin-right: 56px;
    border-radius: 32px;
    background: $gray;
    font-size: 0;
    @include m {
      width: 96px;
      height: 96px;
      margin: 0 auto 24px;
      border-radius: 16px; } }

  &__pic {
    max-width: 100%;
    max-height: 100%; }
  &__details {
    flex-grow: 1; }
  &__product {
    display: block;
    margin-bottom: 16px;
    font-size: 32px;
    line-height: 1.5;
    font-weight: 600;
    color: $bl;
    @include m {
      font-size: 20px;
      line-height: 1.6; } }
  &__price {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    line-height: (40/24);
    font-weight: 600;
    @include m {
      margin-bottom: 40px;
      justify-content: center;
      line-height: 1.5; } }
  &__old {
    margin-right: 8px;
    font-size: 20px;
    text-decoration: line-through;
    color: rgba($bl, .24);
    @include m {
      font-size: 14px; } }
  &__actual {
    font-size: 24px;
    @include m {
      font-size: 16px; } }
  &__control {
    display: flex;
    align-items: center;
    @include m {
      justify-content: center; } }
  &__counter {
    margin-right: 24px; }
  &__remove {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 64px;
    height: 64px;
    border: 2px solid $gray;
    border-radius: 50%;
    font-size: 0;
    transition: border-color .25s;
    @include x {
      position: absolute;
      top: 120px;
      right: 56px; }
    @include t {
      position: static; }
    @include m {
      width: 48px;
      height: 48px; }
    .icon {
      font-size: 20px;
      fill: $bl-dark;
      @include m {
        font-size: 16px; } }
    &:hover {
      border-color: $bl-dark; } }
  &__receipt {
    padding: 54px 56px;
    border-radius: 48px;
    border: 2px solid $gray;
    @include x {
      padding-right: 168px; }
    @include t {
      padding-right: 56px; }
    @include m {
      padding: 24px;
      border-radius: 32px; } }
  &__category {
    margin-bottom: 48px;
    font-size: 32px;
    font-weight: 600;
    @include m {
      margin-bottom: 40px;
      font-size: 20px;
      line-height: 1.6; } }
  &__wrap {
    margin-bottom: 80px;
    @include x {
      display: flex;
      flex-wrap: wrap;
      margin: -48px -48px 48px; }
    @include t {
      display: block;
      margin: 0 0 80px; }
    @include m {
      margin-bottom: 56px; } }
  &__line {
    display: flex;
    @include x {
      flex: 0 0 calc(50% - 96px);
      width: calc(50% - 96px);
      margin: 48px 48px 0; }
    @include t {
      width: 100%;
      margin: 0; }
    @include nl {
      margin-bottom: 48px;
      @include x {
        margin-bottom: 0; }
      @include t {
        margin-bottom: 48px; }
      @include m {
        margin-bottom: 40px; } } }
  &__line &__text {
    font-size: 24px;
    line-height: (40/24);
    @include m {
      font-size: 16px;
      line-height: 1.5; }
    &:first-child {
      margin-right: auto; } }
  &__line_total &__text {
    font-weight: 600; }
  &__receipt &__btn {
    @include x {
      min-width: auto; }
    @include t {
      min-width: 100%; } } }

@include dark {
  .cart {
    &__item {
      border-color: $dark; }
    &__preview {
      background: $dark; }
    &__product {
      color: $dark-wh; }
    &__remove {
      border-color: $dark;
      .icon {
        fill: $wh; }
      &:hover {
        border-color: $dark-wh; } }
    &__old {
      color: rgba($dark-wh, .24); }
    &__receipt {
      border-color: $dark; } } }
