.checkout {
  &__row {
    display: flex;
    @include d {
      display: block; } }
  &__col {
    &:first-child {
      flex-grow: 1;
      padding-right: 96px;
      @include x {
        padding-right: 56px; }
      @include d {
        margin-bottom: 48px;
        padding: 0; }
      @include m {
        margin-bottom: 40px; } }
    &:nth-child(2) {
      flex-shrink: 0;
      width: 552px;
      padding-top: 120px;
      @include x {
        width: 520px; }
      @include d {
        width: 100%;
        padding: 0; } } }
  &__item {
    display: none;
    &:first-child {
      display: block; } }
  &__steps {
    position: relative;
    display: flex;
    margin-bottom: 72px;
    @include x {
      margin-bottom: 64px; }
    @include d {
      max-width: 600px; }
    @include m {
      max-width: 300px;
      margin-bottom: 56px; }
    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      z-index: -2;
      transform: translateY(-50%);
      height: 2px;
      background: $gray; } }
  &__step {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: $wh;
    box-shadow: 0 0 0 4px $wh;
    border: 2px solid $gray;
    font-size: 20px;
    font-weight: 600;
    color: $bl;
    transition: all .25s;
    @include m {
      width: 24px;
      height: 24px;
      box-shadow: 0 0 0 2px $wh;
      font-size: 14px; }
    &:not(:last-child):before {
      content: "";
      position: absolute;
      top: 50%;
      width: 0;
      height: 2px;
      z-index: -1;
      background: $green;
      transition: width .25s; }
    &.active {
      background: $green;
      color: #F7FAFC;
      &:before {
        width: 33.33%; } }
    &:first-child {
      &:before {
        left: 0; } }
    &:nth-child(2) {
      &:before {
        left: 33.33%; } }
    &:nth-child(3) {
      &:before {
        left: 66.66%; } }
    @include nl {
      margin-right: auto; } }
  &__container {
    padding: 56px;
    border: 2px solid $gray;
    border-radius: 48px;
    @include t {
      border-radius: 40px; }
    @include m {
      padding: 24px;
      border-radius: 32px; } }
  &__category {
    margin-bottom: 48px;
    font-size: 32px;
    font-weight: 600;
    line-height: 1.5;
    @include m {
      margin-bottom: 40px;
      font-size: 20px;
      line-height: 1.6; } }
  &__fieldset {
    margin-bottom: 80px;
    @include m {
      margin-bottom: 56px; } }
  &__field,
  &__line {
    @include nl {
      margin-bottom: 48px;
      @include m {
        margin-bottom: 40px; } } }
  &__line {
    display: flex;
    margin: 0 -12px;
    @include a {
      display: block;
      margin: 0; }
    @include nl {
      @include a {
        margin-bottom: 40px; } } }
  &__cell {
    flex: 0 0 calc(50% - 24px);
    width: calc(50% - 24px);
    margin: 0 12px;
    @include a {
      width: 100%;
      margin: 0; }
    @include nl {
      @include a {
        margin-bottom: 40px; } } }
  &__btn {
    @include nl {
      margin-bottom: 24px;
      @include m {
        margin-bottom: 16px; } } }
  &__box {
    display: none;
    padding-top: 32px; }
  &__variants {
    display: flex;
    align-items: center;
    margin-bottom: 48px;
    @include m {
      margin-bottom: 40px; } }
  &__variants &__checkbox {
    @include nl {
      margin-right: 56px;
      @include a {
        margin-right: auto; } } }
  &__el {
    display: none;
    &:first-child {
      display: block; }
    &:nth-child(2) {
      padding-top: 32px;
      @include m {
        padding-top: 16px; } } } }

@include dark {
  .checkout {
    &__steps {
      &:before {
        background: $dark; } }
    &__step {
      box-shadow: 0 0 0 4px $bg-dark;
      border-color: $dark;
      background: $bg-dark;
      color: $dark-wh;
      @include m {
        box-shadow: 0 0 0 2px $bg-dark; }
      &.active {
        border-color: $green;
        background-color: $green; } }
    &__container {
      border-color: $dark; } } }
