.basket {
  border: 2px solid $gray;
  &__category {
    margin-bottom: 48px;
    font-size: 32px;
    font-weight: 600;
    line-height: 1.5;
    @include m {
      margin-bottom: 40px;
      font-size: 20px;
      line-height: 1.6; } }
  &__list {
    margin-bottom: 48px;
    @include m {
      margin-bottom: 40px; } }
  &__item {
    display: flex;
    align-items: center;
    @include m {
      display: block;
      text-align: center; }
    @include nl {
      margin-bottom: 24px;
      @include m {
        margin-bottom: 40px; } } }
  &__preview {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 96px;
    height: 96px;
    border-radius: 16px;
    background: $gray;
    font-size: 0;
    @include m {
      margin: 0 auto 24px; } }
  &__pic {
    max-width: 100%;
    max-height: 100%; }
  &__details {
    flex-grow: 1;
    padding: 0 16px 0 24px;
    @include m {
      margin-bottom: 40px;
      padding: 0; } }
  &__product {
    display: block;
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 1.6;
    font-weight: 600;
    color: $bl; }
  &__price {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 600;
    @include m {
      justify-content: center; } }
  &__old {
    margin-right: 12px;
    font-size: 14px;
    text-decoration: line-through;
    color: rgba($bl, .24); }
  &__remove {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    align-self: flex-start;
    width: 48px;
    height: 48px;
    margin-top: 24px;
    border: 2px solid $gray;
    border-radius: 50%;
    font-size: 0;
    transition: border-color .25s;
    @include m {
      margin: 0; }
    .icon {
      font-size: 14px;
      fill: $bl-dark; }
    &:hover {
      border-color: $bl-dark; } }
  &__total {
    display: flex;
    align-items: center;
    font-weight: 600;
    line-height: (40/24); }
  &__total &__text:first-child {
    margin-right: auto; }
  &__btns {
    display: flex;
    margin: 0 -12px; }
  &__btns &__btn {
    flex: 0 0 calc(50% - 24px);
    min-width: calc(50% - 24px);
    margin: 0 12px; }
  &_checkout {
    padding: 56px;
    border-radius: 48px;
    @include t {
      border-radius: 40px; }
    @include m {
     padding: 24px;
     border-radius: 32px; } }
  &_checkout &__total {
    margin-bottom: 80px;
    font-size: 24px;
    @include m {
      margin-bottom: 56px;
      font-size: 16px; } }
  &_header {
    padding: 24px;
    border-radius: 32px;
    background: $wh; }
  &_header &__total {
    margin-bottom: 24px;
    font-size: 20px; } }

@include dark {
  .basket {
    border-color: $dark;
    &__preview {
      background: $dark; }
    &__product {
      color: $dark-wh; }
    &__old {
      color: rgba($dark-wh, .24); }
    &__remove {
      border-color: $dark;
      .icon {
        fill: $wh; }
      &:hover {
        border-color: $dark-wh; } }
    &_header {
      background: $bg-dark; } } }
