.social {
  display: flex;
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: $gray;
    transition: background .25s;
    @include m {
      width: 24px;
      height: 24px;
      background: none; }
    .icon {
      font-size: 20px;
      fill: $bl-dark; }
    .icon-twitter {
      font-size: 18px; }
    .icon-facebook {
      font-size: 22px; }
    &:hover {
      background: rgba($gray, .75); }
    @include nl {
      margin-right: 16px;
      @include m {
        margin-right: 24px; } } } }

@include dark {
  .social {
    &__link {
      background: $dark;
      @include m {
        background: none; }
      .icon {
        fill: $wh; }
      &:hover {
        background: rgba($dark, .75); } } } }
