.field {
  &__wrap {
    min-height: 64px;
    @include m {
      min-height: 48px; } }
  &__label {
    margin-bottom: 16px;
    line-height: 1.5;
    @include m {
      margin-bottom: 8px;
      font-size: 14px;
      line-height: (24/14); } }
  &__input,
  &__textarea {
    width: 100%;
    border: 2px solid $gray;
    border-radius: 32px;
    @include f;
    font-size: 20px;
    font-weight: 600;
    color: $bl;
    transition: border-color .25s;
    @include m {
      border-radius: 24px;
      font-size: 16px; }
    &:focus {
      border-color: $green; }
    @include placeholder {
      color: rgba($bl, .4); } }
  &__input {
    height: 64px;
    padding: 0 22px;
    @include m {
      height: 48px; } }
  &__textarea {
    height: 192px;
    padding: 18px 22px;
    resize: none;
    @include m {
      height: 144px; } }
  &_textarea &__wrap {
    font-size: 0; } }

.nice-select {
  min-width: 240px;
  height: 64px;
  padding: 0 55px 0 22px;
  border-radius: 32px;
  border: 2px solid $gray;
  line-height: 60px;
  font-size: 20px;
  font-weight: 600;
  color: $bl;
  transition: all .25s;
  @include m {
    min-width: 125px;
    height: 48px;
    border-radius: 24px;
    line-height: 44px;
    font-size: 16px; }
  &:after {
    display: none; }
  &:before {
    content: "";
    position: absolute;
    top: 50%;
    right: 24px;
    transform: translateY(-50%);
    width: 14px;
    height: 8px;
    background: url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z' fill='black'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / auto 100%;
    transition: transform .25s; }
  .list {
    left: -2px !important;
    right: -2px !important;
    padding: 16px 22px;
    border-radius: 0 0 32px 32px;
    border-width: 0 2px 2px;
    border-style: solid;
    border-color: $gray;
    box-shadow: none;
    margin-top: 0;
    transform: none;
    transition: opacity .25s;
    @include m {
      padding: 10px 22px;
      border-radius: 0 0 24px 24px; } }
  &.wide .list {
    left: -2px !important;
    right: -2px !important; }
  .option {
    position: relative;
    min-height: auto;
    padding: 0 25px 0 0;
    background: none;
    white-space: normal;
    font-size: 20px;
    line-height: 32px;
    font-weight: 400;
    color: $bl;
    transition: color .25s;
    @include m {
      font-size: 16px; }
    &:before {
      content: "";
      position: absolute;
      top: 13px;
      right: 2px;
      width: 14px;
      height: 9px;
      background: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.7622 0.35272C14.1197 0.773691 14.0683 1.40476 13.6473 1.76226L5.39729 8.76827C5.02398 9.08529 4.476 9.08529 4.10269 8.76827L0.352689 5.58372C-0.0682814 5.22622 -0.119739 4.59515 0.237755 4.17418C0.595249 3.75321 1.22632 3.70175 1.64729 4.05925L4.74999 6.6941L12.3527 0.237785C12.7737 -0.119709 13.4047 -0.0682508 13.7622 0.35272Z' fill='black'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / auto 100%;
      opacity: 0;
      transition: opacity .25s;
      @include m {
        top: 11px; } }
    &:hover {
      color: rgba($bl, .75); }
    &.selected {
      font-weight: 600;
      &:before {
        opacity: 1; } }
    @include nl {
      margin-bottom: 32px;
      @include m {
        margin-bottom: 12px; } } }
  .option:hover,
  .option.focus,
  .option.selected.focus {
    background: none; }
  &:active,
  &.open,
  &:focus,
  &:hover {
    border-color: $gray; }
  &.open {
    border-radius: 32px 32px 0 0;
    border-bottom-color: transparent;
    @include m {
      border-radius: 24px 24px 0 0; }
    &:before {
      transform: translateY(-50%) rotate(180deg); } } }

select {
  white-space: nowrap;
  height: 64px;
  padding: 0 55px 0 22px;
  border-radius: 32px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 2px solid $gray;
  background: $wh url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z' fill='black'/%3E%3C/svg%3E%0A") no-repeat calc(100% - 24px) 50% / 14px 8px;
  @include f;
  font-size: 20px;
  font-weight: 500;
  color: $bl;
  cursor: pointer;
  transition: all .25s;
  @include m {
    border-radius: 24px; }
  &::-ms-expand {
    display: none; }
  &:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000; } }

@include dark {
  .field {
    &__input,
    &__textarea {
      border-color: $dark;
      background: $bg-dark;
      color: $dark-wh;
      @include placeholder {
        color: rgba($dark-wh, .64); }
      &:focus {
        border-color: $green; } } }
  select {
    border-color: $dark;
    background: $bg-dark url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z' fill='white'/%3E%3C/svg%3E%0A") no-repeat calc(100% - 24px) 50% / 14px 8px;
    color: $dark-wh; }
  .nice-select {
    border-color: $dark;
    background: $bg-dark;
    color: $dark-wh;
    &:before {
      background-image: url("data:image/svg+xml,%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.292893 0.292893C0.683417 -0.0976311 1.31658 -0.0976311 1.70711 0.292893L7 5.58579L12.2929 0.292893C12.6834 -0.0976311 13.3166 -0.0976311 13.7071 0.292893C14.0976 0.683417 14.0976 1.31658 13.7071 1.70711L7.70711 7.70711C7.31658 8.09763 6.68342 8.09763 6.29289 7.70711L0.292893 1.70711C-0.0976311 1.31658 -0.0976311 0.683417 0.292893 0.292893Z' fill='white'/%3E%3C/svg%3E%0A"); }
    .list {
      border-color: $dark;
      background: $bg-dark; }
    .option {
      color: $dark-wh;
      &:before {
        background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.7622 0.35272C14.1197 0.773691 14.0683 1.40476 13.6473 1.76226L5.39729 8.76827C5.02398 9.08529 4.476 9.08529 4.10269 8.76827L0.352689 5.58372C-0.0682814 5.22622 -0.119739 4.59515 0.237755 4.17418C0.595249 3.75321 1.22632 3.70175 1.64729 4.05925L4.74999 6.6941L12.3527 0.237785C12.7737 -0.119709 13.4047 -0.0682508 13.7622 0.35272Z' fill='white'/%3E%3C/svg%3E%0A"); } } } }






