.slider {
  &__title {
    @include t {
      font-size: 32px;
      line-height: 1.5; } }
  &__container {
    margin: 0 -24px;
    @include x {
      margin: 0 -28px; }
    @include t {
      margin: 0 -24px; } }
  &__list {
    visibility: hidden;
    @include m {
      padding-bottom: 88px; }
    &.slick-initialized {
      visibility: visible; } }
  &__slide {
    padding: 0 24px;
    @include d {
      padding: 0 28px; }
    @include t {
      padding: 0 24px; } }
  .slick-arrow {
    position: absolute;
    top: -125px;
    background: $gray;
    border: 2px solid $gray;
    @include x {
      top: -117px; }
    @include t {
      top: -112px; }
    @include m {
      top: auto;
      bottom: 0; }
    &:hover {
      border-color: $bl-dark;
      background: transparent; } }
  .slick-prev {
    right: 88px;
    @include x {
      right: 92px; }
    @include t {
      right: 88px; }
    @include m {
      right: auto;
      left: calc(50% - 56px); } }
  .slick-next {
    right: 24px;
    @include x {
      right: 28px; }
    @include t {
      right: 24px; }
    @include m {
      right: calc(50% - 56px); } } }

@include dark {
  .slider {
    .slick-arrow {
      background: $dark;
      border-color: $dark;
      .icon {
        fill: $wh; }
      &:hover {
        border-color: $dark-wh; } } } }
