.details {
  @include d {
    text-align: center; }
  &__center {
    max-width: 1176px; }
  &__row {
    display: flex;
    @include d {
      display: block; } }
  &__col {
    &:first-child {
      flex-grow: 1;
      padding-right: 72px;
      @include x {
        padding-right: 56px; }
      @include d {
        margin-bottom: 64px;
        padding: 0; }
      @include m {
        margin-bottom: 56px; } }
    &:nth-child(2) {
      flex-shrink: 0;
      width: 496px;
      padding-top: 32px;
      @include d {
        width: 100%;
        padding: 0; } } }
  &__item {
    display: flex;
    @include d {
      display: block; }
    @include nl {
      margin-bottom: 57px;
      @include d {
        margin-bottom: 64px; }
      @include m {
        margin-bottom: 56px; } } }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 64px;
    height: 64px;
    margin-right: 32px;
    border-radius: 50%;
    background: $gray;
    path {
      stroke: $bl-dark; }
    @include d {
      margin: 0 auto 32px; } }
  &__category {
    margin-bottom: 16px;
    font-size: 24px;
    font-weight: 600;
    line-height: (40/24); }
  &__text {
    font-size: 20px;
    line-height: 1.6; } }

@include dark {
  .details {
    &__icon {
      background: $dark;
      path {
        stroke: $wh; } } } }
