.switch {
  display: inline-block;
  position: relative;
  user-select: none;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__in {
    display: block;
    width: 56px;
    height: 32px;
    border: 4px solid $gray;
    background: $gray;
    border-radius: 16px;
    transition: all .25s; }
  &__tick {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    background: $green;
    border-radius: 50%;
    font-size: 0;
    transition: all .25s; }
  &__pic_moon {
    display: none; }
  &__input:checked + &__in {
    border-color: #1E1E27;
    background: #1E1E27; }
  &__input:checked + &__in &__tick {
    transform: translateX(24px); }
  &__input:checked + &__in &__pic_moon {
    display: inline-block; }
  &__input:checked + &__in &__pic_sun {
    display: none; } }

@include dark {
  .switch {
    &__in {
      background: $dark; } } }
