.btn {
  min-width: 165px;
  padding: 0 40px;
  height: 64px;
  border-radius: 32px;
  @include f;
  font-size: 20px;
  font-weight: 700;
  transition: all .25s;
  @include m {
    min-width: 91px;
    height: 48px;
    border-radius: 24px;
    padding: 0 24px;
    font-size: 16px; }
  &_green {
    background: $green;
    color: #F7FAFC;
    &:hover {
      background: lighten(#00CC96, .8); } }
  &_border {
    padding: 0 38px;
    border: 2px solid $gray;
    color: $bl;
    @include m {
      padding: 0 22px; }
    &:hover {
      border-color: $bl-dark; } }
  &_wide {
    min-width: 100%;
    @include m {
      min-width: 100%; } } }

a.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

@include dark {
  .btn {
    &_border {
      border-color: $dark;
      background: $bg-dark;
      color: $dark-wh;
      &:hover {
       border-color: $dark-wh; } } } }
