.products {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -72px -24px 0;
    @include x {
      margin: -64px -28px 0; }
    @include t {
      margin: -64px -24px 0; }
    @include a {
      display: block;
      margin: 0; } }
  &__list .product {
    flex: 0 0 calc(25% - 48px);
    width: calc(25% - 48px);
    margin: 72px 24px 0;
    @include x {
      flex: 0 0 calc(33.333% - 56px);
      width: calc(33.333% - 56px);
      margin: 64px 28px 0; }
    @include t {
      flex: 0 0 calc(50% - 48px);
      width: calc(50% - 48px);
      margin: 64px 24px 0; }
    @include a {
      width: 100%;
      margin: 0; }
    @include nl {
      @include a {
        margin-bottom: 56px; } } }
  &__btns {
    margin-top: 72px;
    text-align: center;
    @include x {
      margin-top: 64px; }
    @include m {
      margin-top: 56px; } }
  &__result {
    margin-bottom: 72px;
    font-size: 20px;
    line-height: 1.6;
    @include x {
      margin-bottom: 64px; }
    @include m {
      margin-bottom: 56px;
      font-size: 16px; } }
  &__counter {
    font-weight: 700; }
  &_main &__title {
    @include t {
      font-size: 32px;
      line-height: 1.5; } }
  &_main &__title,
  &_main &__stage {
    @include m {
      text-align: center; } } }
