.about {
  text-align: center;
  &__center {
    max-width: 1244px; }
  &__title {
    @include t {
      font-size: 32px;
      line-height: 1.5; } }
  &__list {
    display: flex;
    @include d {
      flex-wrap: wrap;
      justify-content: center;
      margin: -64px -32px 0; }
    @include t {
      display: block;
      margin: 0; } }
  &__item {
    flex-shrink: 0;
    width: 304px;
    @include d {
      flex: 0 0 calc(50% - 64px);
      width: calc(50% - 64px);
      margin: 64px 32px 0; }
    @include t {
      width: 100%;
      margin: 0; }
    @include nl {
      margin-right: auto;
      @include d {
        margin-right: 32px; }
      @include t {
        margin: 0 0 64px; }
      @include m {
        margin-bottom: 56px; } } }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    height: 64px;
    margin: 0 auto 32px;
    border-radius: 50%;
    background: $gray;
    font-size: 0;
    path {
      stroke: $bl-dark; } }
  &__category {
    margin-bottom: 16px;
    font-size: 24px;
    line-height: (40/24);
    font-weight: 600; }
  &__text {
    font-size: 20px;
    line-height: (32/20); }
  &__btns {
    margin-top: 72px;
    text-align: center;
    @include d {
      margin-top: 64px; }
    @include m {
      margin-top: 56px; } } }

@include dark {
  .about {
    &__icon {
      background: $dark;
      path {
        stroke: $wh; } } } }
