.work {
  &__row {
    display: flex;
    align-items: flex-start;
    @include d {
      display: block; } }
  &__col {
    &:first-child {
      flex-shrink: 0;
      width: 440px;
      padding: 54px 56px;
      border-radius: 48px;
      border: 2px solid $gray;
      @include d {
        width: 100%;
        margin-bottom: 64px;
        padding: 0;
        border-radius: 0;
        border: none; }
      @include m {
        margin-bottom: 56px; } }
    &:nth-child(2) {
      flex-grow: 1;
      padding-left: 96px;
      @include x {
        padding-left: 64px; }
      @include d {
        padding-left: 0; } } }
  .filters {
    margin-bottom: 0;
    @include x {
      margin-bottom: 0; }
    @include m {
      margin-bottom: 0; } }
  &__item {
    padding: 54px 56px;
    border-radius: 48px;
    border: 2px solid $gray;
    @include t {
      border-radius: 40px; }
    @include m {
      padding: 24px;
      border-radius: 32px; }
    @include nl {
      margin-bottom: 48px;
      @include m {
        margin-bottom: 40px; } } }
  &__post {
    margin-bottom: 24px;
    font-size: 32px;
    line-height: 1.5;
    font-weight: 600;
    @include m {
      margin-bottom: 16px;
      font-size: 20px;
      line-height: 1.6; } }
  &__text {
    margin-bottom: 48px;
    font-size: 20px;
    line-height: 1.6;
    @include m {
      margin-bottom: 40px;
      font-size: 16px;
      line-height: 2; } }
  &__item &__btn {
    min-width: auto; } }

@include dark {
  .work {
    &__col {
      &:first-child {
        border-color: $dark; } }
    &__item {
      border-color: $dark; } } }
