.search {
  position: relative;
  width: 21px;
  overflow: hidden;
  transition: width .25s;
  @include m {
    width: 100%; }
  &__input {
    width: 100%;
    height: 48px;
    padding: 0;
    opacity: 0;
    border: 2px solid $gray;
    border-radius: 24px;
    @include f;
    font-size: 16px;
    color: $bl;
    opacity: padding .25s, opacity .25s;
    @include m {
      padding: 0 22px 0 54px;
      opacity: 1; }
    @include placeholder {
      color: $bl;
      @include m {
        color: rgba($bl, .4); } } }
  &__btn {
    position: absolute;
    top: 52%;
    left: 0;
    transform: translateY(-50%);
    font-size: 0;
    transition: left .25s;
    @include m {
      left: 18px; }
    .icon {
      font-size: 21px;
      fill: $bl-dark;
      transition: fill .25s;
      @include m {
        font-size: 24px; } }
    &:hover .icon {
      fill: rgba($bl-dark, .75); } }
  &.show {
    width: 352px;
    @include d {
      width: 325px; }
    @include m {
      width: 100%; } }
  &.show &__input {
    padding: 0 24px 0 64px;
    opacity: 1;
    @include d {
      padding: 0 16px 0 48px; }
    @include m {
      padding: 0 24px 0 56px; } }
  &.show &__btn {
    left: 24px;
    @include d {
      left: 16px; }
    @include m {
      left: 18px; } } }

@include dark {
  .search {
    &__btn {
      .icon {
        fill: $wh; }
      &:hover .icon {
        fill: rgba($wh, .75); } }
    &__input {
      border-color: $dark;
      background: $bg-dark;
      color: $dark-wh;
      @include placeholder {
        color: $dark-wh; } } } }
